[class^="delete"] {
  position: absolute;
  border: none;
  padding: 10px;
  width: 18px;
  height: 18px;
  right: 21px;
  top: 18px;
  background: transparent;
  z-index: 105;
  display: none;
}

[class^="delete"] .line {
  display: inline-block;
  width: 18px;
  height: 3px;
  background: #fa002a;
  transition: transform 0.4s;
  position: absolute;
  left: 0;
}

[class^="delete"]:hover .line:first-child {
  transform: rotate(45deg);
}

[class^="delete"]:hover .line:nth-child(2) {
  transform: rotate(-45deg);
}

[class^="delete"]:hover {
  cursor: pointer;
}

